import React from "react";
import ReactDOM from "react-dom";
import { ThemeProvider } from "styled-components";
import App from "./App";
import "./index.scss";
import { theme } from "./theme";
import PreviewContainer from "./hooks/usePreviewContainer";
import { ToastContainer } from "react-toastify";
import { DAppProvider } from "@usedapp/core";
import { UseContractState } from "./hooks/useContractState";
import { dappConfig } from "./hooks/config";
import { Router as Router } from "react-router-dom";
import { UseModal } from "./stateful/ModalView";
import { history } from "./hooks/createHistory";
import "react-toastify/dist/ReactToastify.css";

import "video-react/dist/video-react.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-inner-image-zoom/lib/InnerImageZoom/styles.css";

ReactDOM.render(
  <React.StrictMode>
    <UseModal.Provider>
      <DAppProvider config={dappConfig}>
        <ThemeProvider theme={theme}>
          <Router history={history}>
            <ToastContainer />
            <UseContractState.Provider>
              <PreviewContainer.Provider>
                <App />
              </PreviewContainer.Provider>
            </UseContractState.Provider>
          </Router>
        </ThemeProvider>
      </DAppProvider>
    </UseModal.Provider>
  </React.StrictMode>,
  document.getElementById("root")
);

