import React, { useCallback, useMemo } from "react";
import styled from "styled-components";
import { Epoch, NFT } from "../types/NFT";
import { UseContractState } from "../hooks/useContractState";
import Card from "../stateless/Card";
import { TransferModal } from "../modals/TransferModal";
import { UseModal } from "./ModalView";
import { Spinner } from "../stateless/Spinner";

const CollectionDiv = styled.div`
  display: flex;
  flex-flow: column;
  background: ${(props) => props.theme.colors.bg};
  color: ${(props) => props.theme.colors.fg};
  min-height: 100vh;

  padding-left: 200px;
  padding-right: 200px;

  padding-top: 200px;

  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    padding-top: 80px;
  }
`;
const Cards = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const RenderFullPageInner = ({
  next,
  previous,
}: {
  next: (() => void) | undefined;
  previous: (() => void) | undefined;
}) => {
  const { account, epochDetails, loading } = UseContractState.useContainer();
  const { setModal, clearModal } = UseModal.useContainer();

  const showTransferModal = useCallback(
    (params: { epoch: Epoch; nft: NFT; epochSeriesIndex: number }) => {
      setModal(
        <TransferModal
          epoch={params.epoch}
          nft={params.nft}
          epochSeriesIndex={params.epochSeriesIndex}
          hideModal={clearModal}
        />
      );
    },
    [setModal]
  );

  const hasACard = useMemo(() => {
    return epochDetails.reduce((hasAny, epoch) => {
      if (hasAny) {
        return true;
      }
      return !!epoch.nfts.reduce(
        (acc, nft) => acc || (nft.owned ? nft.owned > 0 : false),
        false
      );
    }, false);
  }, [epochDetails]);

  return (
    <CollectionDiv>
      <h2 style={{ marginBottom: 30 }}>Your Collection</h2>
      <Cards>
        {!account ? (
          <p>Log in to see your collection.</p>
        ) : loading ? (
          <div
            style={{
              width: "100%",
              display: "flex",
              flexFlow: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Spinner white={true} size="120px" />
          </div>
        ) : !hasACard ? (
          <>No NFTs.</>
        ) : (
          epochDetails
            .reverse()
            .map((epoch, i) => {
              const epochCards = epoch.nfts.map((nft, epochSeriesIndex) => {
                const ret = [];
                for (let i = 0; i < (nft.owned || 0); i++) {
                  const onClick = () => {
                    showTransferModal({
                      epoch: { ...epoch },
                      nft,
                      epochSeriesIndex,
                    });
                  };
                  ret.push(<Card onClick={onClick} nft={nft} />);
                }
                return ret;
              });

              const hasCards = epochCards.reduce(
                (any, x) => any || x.length > 0,
                false
              );
              if (!hasCards) {
                return;
              }

              return (
                <div
                  style={{
                    width: "100%",
                    marginBottom: "50px",
                  }}
                >
                  <h4 style={{ marginBottom: 10 }}>Epoch {epoch.id}</h4>
                  <div
                    style={{
                      width: "100%",
                      display: "flex",
                      flexWrap: "wrap",
                      justifyContent: "flex-start",
                      alignItems: "center",
                    }}
                  >
                    {epochCards}
                  </div>
                </div>
              );
            })
            .valueSeq()
            .toArray()
        )}
      </Cards>
    </CollectionDiv>
  );
};

export const Collection = () => {
  return <RenderFullPageInner next={undefined} previous={undefined} />;
};
