import React, { useCallback, useEffect, useState } from "react";
import styled from "styled-components";
import { transparentize } from "polished";
import Navbar from "react-bootstrap/esm/Navbar";
import Nav from "react-bootstrap/esm/Nav";
import { useEthers } from "@usedapp/core";
import { Link } from "react-router-dom";

import Logo from "../img/ren.svg";
import { useLocation } from "react-router-dom";
import { UseModal } from "./ModalView";
import { NetworkModal } from "../modals/NetworkModal";
import { AccountModal } from "../modals/AccountModal";
import { Web3Modal } from "../modals/Web3Modal";
import { AboutModal } from "../modals/AboutModal";

const HeaderWrapper = styled.div`
  position: fixed;
  width: 100%;
  z-index: 1;

  padding: 0 0px;

  button,
  a {
    color: #0006 !important;
    opacity: 1;
    text-shadow: 0.5px 0.5px ${(props) => props.theme.colors.fg} !important;
  }

  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    padding: 0;
  }

  img {
    width: 100px;

    @media (max-width: ${(props) => props.theme.grid.maxLg}) {
      width: 80px;
    }
    @media (max-width: ${(props) => props.theme.grid.maxSm}) {
      width: 60px;
    }
  }

  a {
    padding: 10px !important;

    @media (max-width: ${(props) => props.theme.grid.maxSm}) {
      padding: 5px !important;
    }

    & + a {
      margin-left: 20px;
    }

    text-transform: uppercase;
    text-align: center;
    line-height: 1.2em;
    font-size: 1em;

    &.highlighted {
      border: 1px solid ${(props) => props.theme.colors.highlight};
    }
  }
`;

const NavLink: React.FC<{ path: string; title: string }> = ({
  path,
  title,
}) => {
  const location = useLocation();

  return (
    <Nav.Link
      className={location.pathname === path ? "active" : ""}
      href={path}
    >
      {title}
    </Nav.Link>
  );
};

const Header = () => {
  const location = useLocation();
  const { activateBrowserWallet, account, chainId, error } = useEthers();
  const { setModal, clearModal } = UseModal.useContainer();

  const [shownWrongNetwork, setShownWrongNetwork] = useState(true);
  const clearNetworkModal = useCallback(() => {
    clearModal();
    setShownWrongNetwork(false);
  }, [clearModal, setShownWrongNetwork]);

  const connectCallback = useCallback(() => {
    setShownWrongNetwork(false);
    setModal(<Web3Modal hideModal={clearModal} />);
    // activateBrowserWallet()
  }, [setShownWrongNetwork]);

  useEffect(() => {
    if (
      error &&
      error.message.match(/Unsupported chain id/) &&
      !shownWrongNetwork
    ) {
      setShownWrongNetwork(true);
      setModal(
        <NetworkModal
          showWeb3Modal={connectCallback}
          hideModal={clearNetworkModal}
        />
      );
    }
  }, [error, shownWrongNetwork, setShownWrongNetwork, setModal]);

  const showAccountModal = useCallback(() => {
    setModal(<AccountModal hideModal={clearModal} />);
  }, [setModal, clearModal]);

  const showAboutModal = useCallback(() => {
    setModal(<AboutModal hideModal={clearModal} />);
  }, [setModal, clearModal]);

  // const [showingWallets, setShowingWallets] = useState(false)
  // const showWallets = useCallback(() => setShowingWallets(true), [
  //   setShowingWallets,
  // ])
  // const hideWallets = useCallback(() => setShowingWallets(false), [
  //   setShowingWallets,
  // ])

  return (
    <>
      {/* {showingWallets ? <Web3Modal hideWallets={hideWallets} /> : null} */}
      <HeaderWrapper>
        <Navbar expand="lg" className="justify-content-between">
          <Link to="/">
            <Navbar.Brand>
              <img src={Logo} />
            </Navbar.Brand>
          </Link>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse
            id="basic-navbar-nav"
            className="justify-content-end"
          >
            <Nav className="justify-content-end">
              {/* <Nav.Link className={location.pathname} href="#community">
                FAQ
              </Nav.Link> */}
              <a style={{ cursor: "pointer" }}>
                <Nav.Item
                  onClick={showAboutModal}
                  className={location.pathname}
                >
                  About
                </Nav.Item>
              </a>
              <Link to="/collection">
                <Nav.Item className={location.pathname}>Collection</Nav.Item>
              </Link>
              {account ? (
                <Nav.Link className="highlighted" onClick={showAccountModal}>
                  {account.slice(0, 8)}
                </Nav.Link>
              ) : (
                <Nav.Link onClick={connectCallback} className="highlighted">
                  Connect Wallet
                </Nav.Link>
              )}
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </HeaderWrapper>
    </>
  );
};

export default Header;
