import { shortenTransactionHash } from '@usedapp/core'
import React from 'react'
import styled from 'styled-components'
import { getExplorerTransactionLink } from '../hooks/config'
import { Spinner } from '../stateless/Spinner'

const AStyle = styled.a`
  display: flex;
  &:hover {
    text-decoration: underline;
  }
`

export const TransactionSummary = ({ tx }: { tx: any }) => {
  return (
    <div
      style={{
        display: 'flex',
        flexFlow: 'row',
        justifyContent: 'between',
        minWidth: '300px',
      }}
    >
      <AStyle
        href={getExplorerTransactionLink(
          tx.transaction.hash,
          tx.transaction.chainId
        )}
        target="_blank"
        rel="noopener noreferrer"
      >
        {tx.description
          ? tx.description
          : shortenTransactionHash(tx.transaction.hash)}{' '}
      </AStyle>
      <div>
        {!tx.receipt ? (
          <Spinner size={40} />
        ) : tx.transaction.status === 0 ? (
          '✘'
        ) : tx.transaction.status === 1 ? (
          '✔'
        ) : (
          ''
        )}
      </div>
    </div>
  )
}
