import React, { useCallback, useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { MediaType, NFT } from "../types/NFT";
import PreviewContainer from "../hooks/usePreviewContainer";
import InnerImageZoom from "react-inner-image-zoom";
import LazyLoad from "react-lazyload";
import { useIsVisible } from "react-is-visible";
import { Spinner } from "./Spinner";

const CardOuter = styled.div`
  height: auto;
  width: 350px;
  padding: 10px;
  /* border: 0.5px solid black; */

  @media (max-width: ${(props) => props.theme.grid.maxXs}) {
    width: 250px;
    padding: 5px;
  }

  &.modal-img {
    top: 0;
    left: 0;
    position: fixed;
    display: block;
    width: 100vw;
    height: 100vh;
    background: #00000022;

    img {
      max-width: 500px;
    }
  }

  img {
    height: auto;
    width: 100%;
  }
`;

interface CardProps {
  nft: NFT;
  inModal?: boolean;
  onClick?: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const Loader = () => {
  return (
    <div
      style={{
        position: "absolute",
        display: "flex",
        left: "0",
        top: "0",
        width: "100%",
        height: "100%",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 1,
      }}
    >
      <Spinner size={"120px"} white={true} />
    </div>
  );
};

export const RenderArtwork = ({
  nft,
  preview,
  inModal,
}: {
  nft: NFT;
  preview?: boolean;
  inModal?: boolean;
}) => {
  const videoRef = useRef<HTMLVideoElement | undefined>(undefined);
  const isVisible = useIsVisible(videoRef);
  const [loading, setLoading] = useState(true);
  const onLoaded = useCallback(() => {
    setLoading(false);
  }, [setLoading]);

  const image =
    nft.mediaType === MediaType.Video ? (
      <video
        onLoad={onLoaded}
        ref={videoRef as any}
        src={nft.url}
        width="100%"
        style={{
          objectFit: "cover",
          maxWidth: "100%",
          maxHeight: "100%",
          cursor: inModal || preview ? "default" : "zoom-in",
        }}
        poster={nft.poster}
        preload="auto"
        autoPlay={true}
        loop={true}
        playsInline={true}
        muted={true}
        controls={preview}
      />
    ) : nft.mediaType === MediaType.Gif ? (
      <img
        style={{
          cursor: inModal || preview ? "default" : "zoom-in",
          minHeight: 300,
        }}
        onLoad={onLoaded}
        src={nft.url}
      />
    ) : nft.mediaType === MediaType.Image ? (
      (inModal || preview) && false ? (
        <InnerImageZoom
          zoomScale={100}
          onLoad={onLoaded}
          hideHint={true}
          src={nft.url}
        />
      ) : (
        <img
          style={{
            cursor: inModal || preview ? "default" : "zoom-in",
            minHeight: 300,
          }}
          onLoad={onLoaded}
          src={nft.url}
        />
      )
    ) : null;

  // Update every second to ensure
  const [time, setTime] = useState(Date.now());

  useEffect(() => {
    if (nft.mediaType === MediaType.Video) {
      const interval = setInterval(() => setTime(Date.now()), 1000);
      return () => {
        clearInterval(interval);
      };
    }
  }, []);

  const isPaused = videoRef.current && videoRef.current.paused;

  useEffect(() => {
    if (videoRef.current && isPaused && isVisible) {
      videoRef.current.play();
    }
  }, [isPaused, isVisible]);

  return (
    <>
      {/* {loading && nft.mediaType !== MediaType.Video ? <Loader /> : null} */}
      {image}
    </>
  );
};

const Card = (props: CardProps) => {
  return (
    <CardOuter onClick={props.onClick}>
      <LazyLoad
        style={{ minHeight: 300, position: "relative" }}
        height={462}
        offset={100}
        placeholder={<Loader />}
      >
        <RenderArtwork inModal={props.inModal} nft={props.nft} />
      </LazyLoad>
    </CardOuter>
  );
};

export default Card;
