import React, { useCallback, useState } from 'react'
import { Link } from 'react-router-dom'
import PreviewContainer from '../hooks/usePreviewContainer'
import styled from 'styled-components'
import type { Epoch, NFT } from '../types/NFT'
import Card, { RenderArtwork } from './Card'

const DetailedCardOuter = styled.div`
  display: flex;
  flex-flow: column;
`
const DetailedCardBottom = styled.div`
  display: flex;
  margin: 10px 40px;
  width: calc(100% - 40px);

  justify-content: center;
  align-items: center;
`

const ClaimButton = styled.button`
  margin-left: 20px;
  background: none;
  padding: 10px;
  border: 1px solid ${(props) => props.theme.colors.highlight};

  &:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }
`

const Owned = styled(Link)`
  color: ${(props) => props.theme.colors.highlight};
  margin-left: 5px;
  margin-right: 5px;
  :hover {
    color: ${(props) => props.theme.colors.highlight};
  }
`

interface CardProps {
  nft: NFT
  onClaim?: () => void
}

const DetailedCard = ({ nft, onClaim }: CardProps) => {
  const { setImage } = PreviewContainer.useContainer()

  const onClick = useCallback(
    (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      event.preventDefault()
      event.stopPropagation()
      // setShowAsModal((current) => !current)
      setImage(<RenderArtwork nft={nft} preview={true} />)
    },
    [setImage]
  )

  return (
    <>
      <DetailedCardOuter>
        <Card onClick={onClick} nft={nft} />
        <DetailedCardBottom>
          {nft.claimed || 0}
          {nft.owned ? (
            <Owned to="/collection">({nft.owned})</Owned>
          ) : null} / {nft.count || 0} claimed
          {onClaim ? <ClaimButton onClick={onClaim}>Claim</ClaimButton> : null}
        </DetailedCardBottom>
      </DetailedCardOuter>
    </>
  )
}

export default DetailedCard
