import React, { useCallback } from "react";
import { useEthers, useTransactionsContext } from "@usedapp/core";
import styled from "styled-components";
import { Modal } from "./Modal";
import { utils } from "ethers";
import { TransactionSummary } from "../stateless/TransactionSummary";
import useCopyClipboard from "../hooks/useCopyToClipboard";
import { getExplorerAddressLink } from "../hooks/config";

const Title = styled.h4`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 400px;
  max-width: 80vw;
  flex: 1;
`;

const Button = styled.button`
  border: 1px solid ${(props) => props.theme.colors.highlight};
  background: none;

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  padding: 10px;
`;

interface AccountModalProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  hideModal: () => void;
}

export function shortenString(str: string, length = 10) {
  length = Math.min(length, str.length);
  const leftSide = Math.floor((length - 2) / 2) + 2;
  const rightSide = length - leftSide;
  return length === str.length
    ? str
    : str.substring(0, leftSide) +
        "..." +
        str.substring(str.length - rightSide);
}

export function shortenAddress(address: string, length = 10): string {
  try {
    const formattedAddress = utils.getAddress(address);
    return shortenString(formattedAddress, length);
  } catch {
    throw new TypeError("Invalid input, address can't be parsed");
  }
}

export const AccountModal: React.FC<AccountModalProps> = ({
  hideModal,
  ...props
}) => {
  const { account, deactivate, chainId } = useEthers();
  const { transactions } = useTransactionsContext();

  const logout = useCallback(() => {
    deactivate();
    hideModal();
  }, [deactivate, hideModal]);

  const [isCopied, setCopied] = useCopyClipboard(1000);
  const copyAddress = useCallback(() => {
    if (account) {
      setCopied(account);
    }
  }, [account, setCopied]);

  return (
    <Modal
      hideModal={hideModal}
      style={{
        width: 500,
        maxWidth: "90vw",
        display: "flex",
        flexFlow: "column",
        alignItems: "flex-start",
        background: "none",
        padding: 0,
      }}
      {...props}
    >
      <div
        style={{
          width: "100%",
          display: "flex",
          flexFlow: "column",
          alignItems: "flex-start",
          background: "white",
          padding: 20,
        }}
      >
        <div className="flex flex-col">
          <Title className="font-sans text-red text-c18 font-bold">
            Account: {account}
          </Title>
        </div>
        <div
          style={{
            marginTop: "10  px",
            display: "flex",
            flexFlow: "row",
            width: "100%",
            alignItems: "space-between",
            justifyContent: "space-between",
          }}
        >
          <Button
            className={`${
              !isCopied ? "cursor-pointer hover:underline" : "cursor-default"
            } outline-none font-sans sm:ml-auto flex items-center`}
            // outline-none wasn't working
            onClick={copyAddress}
          >
            {isCopied ? <>Copied</> : <>Copy address</>}
          </Button>
          <a href={getExplorerAddressLink(account || "", chainId)}>
            <Button>View on Etherscan</Button>
          </a>
          <Button onClick={logout}>Logout</Button>
        </div>
      </div>
      <div
        style={{
          background: "white",
          padding: 20,
          width: "100%",
          marginTop: 8,
        }}
      >
        <div>
          <h4>Transaction History</h4>{" "}
        </div>
        <ul
          style={{
            maxHeight: "60vh",
            overflowY: "auto",
            display: "flex",
            flexFlow: "column",
            marginBottom: 0,
          }}
        >
          {transactions && chainId && transactions[chainId]
            ? (transactions as any)[chainId].map((tx: any) => {
                return (
                  <li>
                    <TransactionSummary tx={tx} />
                  </li>
                );
              })
            : null}
        </ul>
      </div>
    </Modal>
  );
};
