import React, { useCallback, useState } from 'react'
import { shortenTransactionHash, useEthers } from '@usedapp/core'
import { Modal } from './Modal'
import { TransactionStatus } from '../hooks/useContractFunction'
import styled from 'styled-components'
import { Spinner } from '../stateless/Spinner'
import { Link } from 'react-router-dom'
import { getExplorerTransactionLink, sleep } from '../hooks/config'

interface TransactionModalProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  submitCallback?: () => void
  status: TransactionStatus
  hideModal: () => void
  buttonLabel?: string
  submitting?: boolean
  noLine?: boolean
  disabled?: boolean
}

const Button = styled.button`
  background: ${(props) => props.theme.colors.highlight};
  padding: 10px 40px;
  border: none;
  color: ${(props) => props.theme.colors.bg} !important;

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  & + button {
    margin-left: 10px;
  }
`

export const TransactionModal: React.FC<TransactionModalProps> = ({
  status,
  submitCallback,
  hideModal,
  buttonLabel,
  children,
  noLine,
  submitting: submittingProp,
  disabled,
  ...props
}) => {
  const { chainId } = useEthers()
  const [submitting, setSubmitting] = useState(false)
  const [error, setError] = useState<string | undefined>()
  const onClick = useCallback(async () => {
    if (!submitCallback) {
      return
    }
    setError(undefined)
    await sleep(0)
    setSubmitting(true)
    try {
      await submitCallback()
    } catch (error) {
      setError(String(error.message || error))
    }
    setSubmitting(false)
  }, [submitCallback, setSubmitting])

  const disableButton =
    !submitCallback || submitting || submittingProp || disabled

  return (
    <Modal hideModal={hideModal} {...props}>
      <div>{children}</div>
      <div
        style={{
          display: 'flex',
          flexFlow: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          borderTop: noLine ? '' : '',
          width: '100%',
        }}
      >
        {error ? (
          <p
            className="text-red border-red border-2 p-2 m-2 text-center mt-4"
            style={{ width: '100%' }}
          >
            {error}
          </p>
        ) : null}
        {status.status === 'Fail' || status.status === 'Exception' ? (
          <p
            className="text-red border-red border-2 p-2 m-2 text-center mt-4"
            style={{ width: '100%' }}
          >
            {status.status === 'Exception' && status.rejected ? (
              <>Transaction rejected.</>
            ) : (
              <>{status.errorMessage || 'An error occured'}</>
            )}
          </p>
        ) : null}
        {status.status === 'Success' ? (
          <div
            className="flex flex-col items-center justify-center"
            style={{
              width: '100%',
              display: 'flex',
              flexFlow: 'column',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <span style={{ marginBottom: '10px' }}>
              {buttonLabel} successful -{' '}
              <a
                href={getExplorerTransactionLink(
                  status.transaction.hash,
                  status.transaction.chainId || chainId
                )}
                target="_blank"
                rel="noopener noreferrer"
              >
                {shortenTransactionHash(status.transaction.hash)}
              </a>
            </span>
            <Link onClick={hideModal} to="/collection" className="underline">
              View collection.
            </Link>
          </div>
        ) : status.status === 'Mining' ? (
          <>
            <div
              style={{
                minWidth: '300px',
                width: '100%',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                flexFlow: 'row',
              }}
            >
              Confirming...
              <Spinner size={40} />
              <span>
                <a
                  href={getExplorerTransactionLink(
                    status.transaction.hash,
                    status.transaction.chainId || chainId
                  )}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {shortenTransactionHash(status.transaction.hash)}
                </a>
              </span>
            </div>
          </>
        ) : (
          <Button
            onClick={onClick}
            style={{
              width: '100%',
            }}
            disabled={disableButton}
          >
            {status.status === 'Fail' || status.status === 'Exception'
              ? 'Retry'
              : submitting || submittingProp
              ? 'Submitting...'
              : buttonLabel || 'Submit transaction'}
          </Button>
        )}
      </div>
    </Modal>
  )
}
