import React, { useCallback, useEffect, useState } from 'react'

import { useEthers } from '@usedapp/core'
import { getAddress } from '@ethersproject/address'
import { TransactionModal } from './TransactionModal'
import { useClaim } from '../hooks/useClaim'
import type { Epoch, NFT } from '../types/NFT'
import Card from '../stateless/Card'

interface ClaimModalProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  epoch: Epoch
  nft: NFT
  epochSeriesIndex: number

  hideModal: () => void
}

export const ClaimModal: React.FC<ClaimModalProps> = ({
  title,
  epoch,
  nft,
  epochSeriesIndex,
  hideModal,
  ...props
}) => {
  const { account } = useEthers()

  const { claim, state } = useClaim()

  const [submitting, setSubmitting] = useState(false)
  const submitCallback = useCallback(async () => {
    setSubmitting(true)
    try {
      await claim(epoch.id, epochSeriesIndex)
      setSubmitting(false)
    } catch (error) {
      setSubmitting(false)
      throw error
    }
  }, [account, claim, epoch, epochSeriesIndex])

  return (
    <TransactionModal
      buttonLabel={`Claim`}
      submitCallback={submitCallback}
      status={state}
      submitting={submitting}
      noLine={true}
      hideModal={hideModal}
      style={{
        padding: 30,
      }}
      {...props}
    >
      <Card inModal={true} nft={nft} />
    </TransactionModal>
  )
}
