import React, { useCallback, useEffect, useState } from 'react'
import { createContainer } from 'unstated-next'

/**
 * useModalContainer() returns live stats about the collections and about the logged-in
 * account's NFT balances.
 */
function useModalContainer() {
  const [modal, setModal] = useState<React.ReactNode | undefined>(undefined)
  const clearModal = useCallback(() => {
    setModal(undefined)
  }, [setModal])

  return {
    modal,
    setModal,
    clearModal,
  }
}

export const UseModal = createContainer(useModalContainer)

export const ModalView = () => {
  const { modal } = UseModal.useContainer()
  return <>{modal}</>
}
