import { useEthers } from '@usedapp/core'
import { Contract } from '@ethersproject/contracts'
import { ethers } from 'ethers'
import { useMemo } from 'react'
import { AddressZero } from '@ethersproject/constants'
import { JsonRpcSigner, Web3Provider } from '@ethersproject/providers'

import NFTPolygonJSON from './ABIs/NFTPolygon.json'
import ClaimJSON from './ABIs/Claim.json'
import { isAddress } from 'ethers/lib/utils'
import { PolygonNetworks } from './types'

export const NFTPolygonABI = new ethers.utils.Interface(NFTPolygonJSON)
export const ClaimABI = new ethers.utils.Interface(ClaimJSON)

const NFTPolygonAddress = {
  [PolygonNetworks.Mainnet]: '0x813fce94d1855BA440186c3b201a67BF13ae1a5a',
  [PolygonNetworks.Mumbai]: '0xb0b67A96aDbEb7Bb8294b2D388Ec5a75aCe3f600',
}
const ClaimAddress = {
  [PolygonNetworks.Mainnet]: '0x9Fd3DDb52EFa9718a8c4b807D836a011a99E8c19',
  [PolygonNetworks.Mumbai]: '0x8A06Bf92725a9F60dA6579193d0E97AC6A39D898',
}

// account is not optional
export function getSigner(
  library: Web3Provider,
  account: string
): JsonRpcSigner {
  return library.getSigner(account).connectUnchecked()
}

export function getProviderOrSigner(
  library: Web3Provider,
  account?: string
): Web3Provider | JsonRpcSigner {
  return account ? getSigner(library, account) : library
}

export function getContract(
  address: string,
  ABI: any,
  library: Web3Provider,
  account?: string
): Contract {
  if (!isAddress(address) || address === AddressZero) {
    throw Error(`Invalid 'address' parameter '${address}'.`)
  }

  return new Contract(
    address,
    ABI,
    getProviderOrSigner(library, account) as any
  )
}

export function useContract(
  address: string | undefined,
  ABI: any,
  withSignerIfPossible = true
): Contract | null {
  const { library, account } = useEthers()

  return useMemo(() => {
    if (!address || !ABI || !library) return null
    try {
      return getContract(
        address,
        ABI,
        library,
        withSignerIfPossible && account ? account : undefined
      )
    } catch (error) {
      console.error('Failed to get contract', error)
      return null
    }
  }, [address, ABI, library, withSignerIfPossible, account])
}

export const useClaimContract = (
  network: PolygonNetworks | number | undefined
) => useContract(ClaimAddress[network as PolygonNetworks], ClaimJSON, true)

export const useNFTPolygonContract = (
  network: PolygonNetworks | number | undefined
) =>
  useContract(
    NFTPolygonAddress[network as PolygonNetworks],
    NFTPolygonJSON,
    true
  )
