import React, { useCallback, useMemo, useState } from "react";

import { useEthers } from "@usedapp/core";
import { getAddress } from "@ethersproject/address";
import { TransactionModal } from "./TransactionModal";
import { NFT, Epoch } from "../types/NFT";
import { useContractFunction } from "../hooks/useContractFunction";
import { useNFTPolygonContract } from "../hooks/useContract";
import Card from "../stateless/Card";
import styled from "styled-components";

const Input = styled.input`
  border: 1px solid ${(props) => props.theme.colors.highlight};
  width: 100%;
  margin-bottom: 10px;
`;

export function isAddress(value: any): string | false {
  try {
    return getAddress(value);
  } catch {
    return false;
  }
}

interface TransferModalProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  epoch: Epoch;
  nft: NFT;
  epochSeriesIndex: number;

  hideModal: () => void;
}

const isENS = (address: string | undefined): boolean => {
  return !!(
    address &&
    address.length > 4 &&
    address.toLowerCase().slice(-4) === ".eth"
  );
};

export const TransferModal: React.FC<TransferModalProps> = ({
  nft,
  hideModal,
  ...props
}) => {
  const { chainId, account } = useEthers();
  const nftContract = useNFTPolygonContract(chainId)!;

  const [recipient, setRecipient] = useState("");
  const recipientIsValid = useMemo(
    () => isENS(recipient) || isAddress(recipient),
    [recipient]
  );
  const onChange = useCallback(
    (e) => {
      setRecipient(e.target.value || "");
    },
    [setRecipient]
  );

  const { send, state } = useContractFunction(nftContract, "safeTransferFrom");

  const [submitting, setSubmitting] = useState(false);
  const submitCallback = useCallback(async () => {
    setSubmitting(true);
    try {
      await send(
        `Transfer ${nft.name || "NFT"}`,
        account,
        recipient,
        nft.tokenId,
        1,
        []
      );
      setSubmitting(false);
    } catch (error) {
      setSubmitting(false);
      throw error;
    }
  }, [nft, recipient, account, send]);

  const handleKeypress = useCallback(
    (e) => {
      if (e.key === "Enter") {
        submitCallback();
      }
    },
    [submitCallback]
  );

  return (
    <TransactionModal
      buttonLabel={`Transfer`}
      submitCallback={recipientIsValid ? submitCallback : undefined}
      status={state}
      submitting={submitting}
      noLine={true}
      hideModal={hideModal}
      style={{
        padding: 30,
      }}
      {...props}
    >
      <div
        style={{
          width: 400,
          maxWidth: "90vw",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Card inModal={true} nft={nft} />
      </div>
      <h3 className="font-bebas text-left text-black text-3xl mt-c16 break-words">
        {`Transfer ${nft.name || "NFT"}`}
      </h3>
      <Input
        className="bg-white border-black border-2 text-black py-2 px-2 rounded flex-grow-0 mt-c16 w-full"
        type="text"
        value={recipient}
        onChange={onChange}
        placeholder="Recipient"
        onKeyPress={handleKeypress}
        disabled={submitting}
      />
    </TransactionModal>
  );
};
