export const getExplorerTransactionLink = (
  txHash: string,
  chainId: number | undefined
): string | undefined => {
  if (chainId === PolygonNetworks.Mainnet) {
    return `https://explorer-mainnet.maticvigil.com/tx/${txHash}`;
  }
  if (chainId === PolygonNetworks.Mumbai) {
    return `https://explorer-mumbai.maticvigil.com/tx/${txHash}`;
  }
  return undefined;
};

export const getExplorerAddressLink = (
  address: string,
  chainId: number | undefined
): string | undefined => {
  if (chainId === PolygonNetworks.Mainnet) {
    return `https://explorer-mainnet.maticvigil.com/address/${address}`;
  }
  if (chainId === PolygonNetworks.Mumbai) {
    return `https://explorer-mumbai.maticvigil.com/address/${address}`;
  }
  return undefined;
};

export const toBigNumber = (
  bn: EthersBN | undefined | null
): any | undefined => {
  return bn ? new BigNumber(bn.toString()) : undefined;
};

export const toBigNumbers = (
  bns: Array<EthersBN> | undefined | null
): any[] | undefined => {
  return bns ? bns.map((x) => toBigNumber(x)!) : undefined;
};

export const toNumber = (
  bn: EthersBN | undefined | null
): number | undefined => {
  return bn ? bn.toNumber() : undefined;
};

export const toNumbers = (
  bns: Array<EthersBN> | undefined | null
): number[] | undefined => {
  return bns ? bns.map((x) => toNumber(x)!) : undefined;
};

export const formatEther = (
  value: any | undefined,
  round: "UP" | "DOWN",
  digits = 4
): string | undefined => formatToken(value, 18, round, digits);

export const formatToken = (
  value: any | undefined,
  decimals: number | undefined,
  round: "UP" | "DOWN",
  digits = 4
): string | undefined =>
  value !== undefined && decimals !== undefined
    ? value.isZero()
      ? "0"
      : value
          .dividedBy(new BigNumber(10).exponentiatedBy(decimals))
          .decimalPlaces(
            digits,
            round === "UP" ? BigNumber.ROUND_UP : BigNumber.ROUND_DOWN
          )
          .toFixed()
    : undefined;

export const sleep = (ms: number) =>
  new Promise((resolve) => setTimeout(resolve, ms));
