export enum MediaType {
  Video = 'video',
  Image = 'image',
  Gif = 'gif',
}

export interface NFT {
  name: string
  url: string
  mediaType: MediaType | string
  poster?: string
  title?: string

  // Config
  zoom?: number
  controls?: boolean

  // Live data
  claimed?: number
  count: number
  tokenId?: number
  owned?: number
}

export interface Epoch {
  id: number
  nfts: NFT[]
  light?: boolean
  darknodes: number
  operatorsCount: number
  artist?: string
  artistLink?: string
  title?: string
  operators: { [operator: string]: number }

  // Live data
  merkleRoot?: Buffer
  remaining?: number[]
  tokenIds?: number[]
  tokenUrls?: string[]
}
