import React, { useCallback, useMemo, useState } from 'react'
import styled from 'styled-components'

import Card from '../stateless/Card'
import DetailedCard from '../stateless/DetailedCard'
import type { Epoch, NFT } from '../types/NFT'

import Container from '../styled/Container'
import CardSeries from '../stateless/CardSeries'
import { ClaimModal } from '../modals/ClaimModal'
import { useEthers } from '@usedapp/core'
import { UseModal } from './ModalView'
import { ExternalLink } from '../stateless/ExternalLink'
import ReactTooltip from 'react-tooltip'

const EpochDiv = styled.div<{ light: boolean | undefined }>`
  display: flex;
  flex-flow: column;

  height: 100%;

  padding-top: 200px;

  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    padding-top: 80px;
  }

  color: ${(props) =>
    props.light ? props.theme.colors.bg : props.theme.colors.fg};

  button {
    color: ${(props) =>
      props.light ? props.theme.colors.bg : props.theme.colors.fg};
  }
`

const EpochTopTop = styled.div`
  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    display: none;
  }

  padding: 0 100px;
  min-width: 500px;
  @media (max-width: ${(props) => props.theme.grid.maxLg}) {
    min-width: 400px;
  }
  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    padding: 0 20px;
    min-width: 200px;
  }
  @media (max-width: ${(props) => props.theme.grid.maxXs}) {
    min-width: 150px;
  }
  padding-right: 20px;
  margin-bottom: 30px;
`

const EpochTopTopMobile = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;

  @media (min-width: ${(props) => props.theme.grid.minMd}) {
    display: none;
  }
`

const EpochBottom = styled.div`
  display: flex;
  flex-flow: column;
  padding: 20px 100px;

  @media (max-width: ${(props) => props.theme.grid.maxSm}) {
    display: none;
  }
`

const EpochRight = styled.div`
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  max-width: 100vw;
  /* align-items: center; */
`

const EpochTitle = styled.h3`
  font-size: 3em;

  @media (max-width: ${(props) => props.theme.grid.maxLg}) {
    font-size: 2em;
  }
`

const Highlight = styled.span`
  color: ${(props) => props.theme.colors.highlight};
`

const NavButton = styled.button`
  background: ${(props) => props.theme.colors.highlight};
  padding: 10px 40px;
  border: none;
  color: ${(props) => props.theme.colors.bg} !important;

  & + button {
    margin-left: 10px;
  }
`

const TooltipOuter = styled.div`
  display: inline-block;
  > div {
    max-width: 300px;
    text-align: center;
  }
`
const TooltipA = styled.a`
  border: 1px solid ${(props) => props.theme.colors.highlight};
  padding: 2px;
  color: black;
  width: 25px;
  display: inline-block;
  text-align: center;
  :hover {
    text-decoration: none;
    color: black;
    cursor: pointer;
  }
`

const Tooltip: React.FC = ({ children }) => {
  return (
    <TooltipOuter>
      <TooltipA data-tip="React-tooltip">i</TooltipA>
      <ReactTooltip place="top" type="light" effect="solid">
        {children}
      </ReactTooltip>
    </TooltipOuter>
  )
}

export const ShowEpoch = ({
  next,
  previous,
  epoch,
  i,
  ...props
}: {
  epoch: Epoch
  i: number
  next?: () => void
  previous?: () => void
} & React.DetailedHTMLProps<
  React.HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>) => {
  const { account } = useEthers()

  const { setModal, clearModal } = UseModal.useContainer()
  const showClaimModal = useCallback(
    (params: { epoch: Epoch; nft: NFT; epochSeriesIndex: number }) => {
      setModal(
        <ClaimModal
          epoch={params.epoch}
          nft={params.nft}
          epochSeriesIndex={params.epochSeriesIndex}
          hideModal={clearModal}
        />
      )
    },
    [setModal]
  )

  const accountCount = useMemo(
    () =>
      account && epoch ? epoch.operators[account.toLowerCase()] : undefined,
    [account, epoch]
  )

  const accountClaimed = useMemo(
    () =>
      account && epoch
        ? epoch.nfts.reduce((sum, nft) => sum + (nft.owned || 0), 0)
        : undefined,
    [account, epoch]
  )

  return (
    <>
      <EpochDiv light={epoch.light} {...(props as any)}>
        <EpochRight className="col-xl-9">
          <EpochTopTopMobile>
            <EpochTitle>
              EPOCH{' '}
              <span
                style={{
                  backgroundColor: '#DCB84C',
                  padding: 10,
                  marginLeft: -10,
                }}
              >
                {epoch.id}
              </span>
            </EpochTitle>
            {epoch.artist ? (
              <span style={{ fontSize: 18, textAlign: 'right' }}>
                by{' '}
                <ExternalLink
                  target="_blank"
                  href={epoch.artistLink}
                  style={{ color: '#DCB84C' }}
                >
                  @{epoch.artist}
                </ExternalLink>
              </span>
            ) : null}
            {account && accountCount && accountCount > 0 ? (
              <>
                <br />
                <span>
                  <Highlight>
                    {accountClaimed}/{accountCount}
                  </Highlight>{' '}
                  claimed by <Highlight>{account.slice(0, 8)}</Highlight>
                </span>
              </>
            ) : null}
          </EpochTopTopMobile>
          <CardSeries className="no-scrollbar">
            <EpochTopTop>
              {epoch.title ? (
                <div>
                  <EpochTitle
                    style={{ textTransform: 'uppercase', marginBottom: 0 }}
                  >
                    {epoch.title}
                  </EpochTitle>
                </div>
              ) : (
                <EpochTitle>
                  EPOCH{' '}
                  <span
                    style={{
                      backgroundColor: '#DCB84C',
                      padding: 10,
                      marginLeft: -10,
                    }}
                  >
                    {epoch.id}
                  </span>
                </EpochTitle>
              )}
              {epoch.title ? (
                <>
                  <span>
                    epoch {epoch.id} - artist feature{' '}
                    <Tooltip>
                      Artist features are drops by established or upcoming NFT
                      artists, featuring artworks that aren't necessarily
                      Ren-themed.
                    </Tooltip>
                  </span>
                  <br />
                </>
              ) : null}
              {epoch.artist ? (
                <div style={{ marginBottom: '20px' }}>
                  by{' '}
                  <ExternalLink
                    target="_blank"
                    href={epoch.artistLink}
                    style={{ color: '#DCB84C' }}
                  >
                    @{epoch.artist}
                  </ExternalLink>
                  .
                </div>
              ) : null}
              {epoch.darknodes} darknodes.
              <br />
              {epoch.operatorsCount} operators.
              {account &&
              accountCount &&
              accountCount > 0 &&
              accountClaimed !== undefined ? (
                <>
                  <br />
                  <Highlight>
                    {accountClaimed}/{accountCount}
                  </Highlight>{' '}
                  claimed by <Highlight>{account.slice(0, 8)}</Highlight>.
                </>
              ) : null}
            </EpochTopTop>

            {epoch.nfts.map((nft, epochSeriesIndex) => {
              const onClaim = () => {
                showClaimModal({ epoch: { ...epoch }, nft, epochSeriesIndex })
              }
              return (
                <DetailedCard
                  key={nft.name}
                  nft={nft}
                  onClaim={
                    account &&
                    accountCount &&
                    accountClaimed !== undefined &&
                    accountClaimed < accountCount
                      ? onClaim
                      : undefined
                  }
                />
              )
            })}
          </CardSeries>
        </EpochRight>
        <EpochBottom>
          <div style={{ marginTop: 20 }}>
            <NavButton onClick={previous}>previous</NavButton>
            {next ? <NavButton onClick={next}>next</NavButton> : null}
          </div>
        </EpochBottom>
      </EpochDiv>
    </>
  )
}
