import { useState } from 'react'
import {
  TransactionReceipt,
  TransactionResponse,
} from '@ethersproject/abstract-provider'
import {
  ChainId,
  shortenTransactionHash,
  useEthers,
  useTransactionsContext,
} from '@usedapp/core'
import { Contract } from 'ethers'
import { toast } from 'react-toastify'

export type TransactionStatus =
  | {
      status: 'None'
    }
  | {
      status: 'Mining'
      chainId: ChainId
      transaction: TransactionResponse
    }
  | {
      status: 'Success'
      chainId: ChainId
      transaction: TransactionResponse
      receipt: TransactionReceipt
    }
  | {
      status: 'Fail'
      transaction: TransactionResponse
      receipt: TransactionReceipt
      chainId: ChainId
      errorMessage: string
    }
  | {
      status: 'Exception'
      chainId: ChainId
      errorMessage: string
      rejected?: boolean
    }

export function useContractFunction(contract: Contract, functionName: string) {
  const [state, setState] = useState<TransactionStatus>({ status: 'None' })
  const { addTransaction } = useTransactionsContext()
  const { chainId } = useEthers()

  let transaction: TransactionResponse

  const send = async (
    description: string,
    ...args: any[]
  ): Promise<boolean> => {
    if (!chainId) {
      return false
    }

    try {
      transaction = await contract[functionName](...args)
      setState({ transaction, status: 'Mining', chainId })
      const date = Date.now()
      addTransaction({
        transaction: {
          ...transaction,
          chainId,
        },
        submittedAt: date,
        // @ts-expect-error
        description,
      })

      const receipt = await transaction.wait()
      if (receipt.status === 0) {
        toast(
          `Error: ${description} (${shortenTransactionHash(transaction.hash)})`,
          { autoClose: 10000, type: 'error' }
        )
      } else {
        toast(
          `Confirmed: ${description} (${shortenTransactionHash(
            transaction.hash
          )})`,
          { autoClose: 10000 }
        )
      }
      setState({ receipt, transaction, status: 'Success', chainId })
      return true
    } catch (e) {
      console.error(e)
      if (transaction) {
        setState({
          status: 'Fail',
          transaction,
          receipt: e.receipt,
          errorMessage: e.reason || e.message,
          chainId,
        })
      } else {
        setState({
          status: 'Exception',
          errorMessage: e.reason || e.message,
          chainId,
          rejected: e?.code === 4001,
        })
      }
      return false
    }
  }

  return { send, state }
}
