import React, { useCallback, useState } from 'react'
import styled from 'styled-components'

import { Modal } from './Modal'

const Button = styled.button`
  background: ${(props) => props.theme.colors.highlight};
  padding: 10px 40px;
  border: none;
  color: ${(props) => props.theme.colors.bg} !important;

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  & + button {
    margin-left: 10px;
  }
`

interface ClaimModalProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  showWeb3Modal: () => void
  hideModal: () => void
}

export const NetworkModal: React.FC<ClaimModalProps> = ({
  title,
  hideModal,
  showWeb3Modal,
  ...props
}) => {
  const hasEthereum = (window as any).ethereum

  const [suggesting, setSuggesting] = useState(false)

  const suggestPolygon = useCallback(async () => {
    setSuggesting(true)
    try {
      await (window as any).ethereum.sendAsync({
        method: 'wallet_addEthereumChain',
        params: [
          {
            chainId: '0x89', // A 0x-prefixed hexadecimal string
            chainName: 'Polygon',
            nativeCurrency: {
              name: 'MATIC',
              symbol: 'MATIC', // 2-6 characters long
              decimals: 18,
            },
            rpcUrls: ['https://rpc-mainnet.maticvigil.com/'],
            blockExplorerUrls: ['https://explorer-mainnet.maticvigil.com/'],
            iconUrls: [], // Currently ignored.
          },
        ],
      })
      showWeb3Modal()
    } catch (error) {
      console.error(error)
    }
    setSuggesting(false)
  }, [])

  return (
    <Modal
      hideModal={hideModal}
      style={{
        padding: 30,
      }}
      {...props}
    >
      <div
        style={{
          display: 'flex',
          flexFlow: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          maxWidth: 400,
        }}
      >
        <h5 style={{ marginBottom: '20px' }}>Connect to Polygon</h5>
        <p style={{ textAlign: 'center', marginBottom: '20px' }}>
          NFTs.REN has been deployed on Ethereum and then bridged to Polygon
          (MATIC) to reduce claiming gas fees. Bridging back to Ethereum will be
          provided soon.
          <br />
          <br />
          Darknode operators have been airdropped 0.05 MATIC to cover claiming
          fees.
        </p>
      </div>

      {hasEthereum ? (
        <Button disabled={suggesting} onClick={suggestPolygon}>
          Add Polygon to MetaMask
        </Button>
      ) : null}
    </Modal>
  )
}
