import { useContractCall, useEtherBalance, useEthers } from "@usedapp/core";
import { createContainer } from "unstated-next";

import BigNumber from "bignumber.js";
import {
  NFTPolygonABI,
  ClaimABI,
  useNFTPolygonContract,
  useClaimContract,
} from "./useContract";

import { useConnectWeb3OnLoad } from "./useConnectWeb3OnLoad";
import { Buffer } from "buffer";

import EpochData1 from "./merkle/1.json";
import EpochData2 from "./merkle/2.json";
import EpochData3 from "./merkle/3.json";
import EpochData4 from "./merkle/4.json";
import EpochData5 from "./merkle/5.json";
import EpochData6 from "./merkle/6.json";
import EpochData7 from "./merkle/7.json";
import EpochData8 from "./merkle/8.json";
import EpochData9 from "./merkle/9.json";
import EpochData10 from "./merkle/10.json";
import EpochData11 from "./merkle/11.json";
import EpochData12 from "./merkle/12.json";
import EpochData13 from "./merkle/13.json";
import EpochData14 from "./merkle/14.json";
import { Epoch } from "../types/NFT";
import { OrderedMap } from "immutable";

const epochData = OrderedMap<number, Epoch>()
  .set(1, EpochData1)
  .set(2, EpochData2)
  .set(3, EpochData3)
  .set(4, EpochData4)
  .set(5, EpochData5)
  .set(6, EpochData6)
  .set(7, EpochData7)
  .set(8, EpochData8)
  .set(9, EpochData9)
  .set(10, EpochData10)
  .set(11, EpochData11)
  .set(12, EpochData12)
  .set(13, EpochData13)
  .set(14, EpochData14);

export interface AccountNFT {
  tokenId: any; // BigNumber
}

export const tokenIdSeparator = 100000000;

const decodeBalance = ([balances, tokenIds]: [any[], any[]] | []):
  | OrderedMap<number, OrderedMap<number, number>>
  | undefined => {
  if (!balances || !tokenIds) {
    return undefined;
  }

  let balanceMap = OrderedMap<number, OrderedMap<number, number>>();
  for (let i = 0; i < balances.length; i++) {
    const tokenId = tokenIds[i].toNumber();
    const epochId = Math.floor(tokenId / tokenIdSeparator);
    const index = (tokenId % tokenIdSeparator) - 1;
    let epochBalances = balanceMap.get(epochId) || OrderedMap<number, number>();
    epochBalances = epochBalances.set(index, balances[i].toNumber());

    balanceMap = balanceMap.set(epochId, epochBalances);
  }
  return balanceMap;
};

const decodeEpochData = (
  data: any[] | undefined,
  balances: OrderedMap<number, OrderedMap<number, number>> | undefined
): OrderedMap<number, Epoch> => {
  if (!data) {
    return epochData;
  }

  let newEpochData = epochData;
  for (const epoch of data) {
    const id = epoch.id.toNumber();
    const current = epochData.get(id, undefined)!;

    const remaining = epoch.remaining.map((x: any) => x.toNumber());
    const tokenIds = epoch.remaining.map((x: any) => x.toNumber());

    const nfts = current.nfts.map((nft, i) => ({
      ...nft,
      claimed: nft.count - remaining[i],
      owned: balances ? balances.getIn([id, i], undefined) : undefined,
      tokenId: id * tokenIdSeparator + i + 1,
      title: current.title,
    }));

    newEpochData = newEpochData.set(id, {
      ...current,
      nfts,
      id: id,
      merkleRoot: Buffer.from(epoch.merkleRoot.slice(2), "hex"),
      remaining: epoch.remaining.map((x: any) => x.toNumber()),
      tokenIds: epoch.remaining.map((x: any) => x.toNumber()),
      tokenUrls: epoch.tokenUrls,
    });
  }

  return newEpochData;
};

/**
 * useContractStateContainer() returns live stats about the collections and about the logged-in
 * account's NFT balances.
 */
function useContractStateContainer() {
  // Try to connect to the injected web3 provider on load.
  useConnectWeb3OnLoad();

  const { account, chainId } = useEthers();

  const claimContract = useClaimContract(chainId);
  const nftPolygonContract = useNFTPolygonContract(chainId);

  // Fetch claim contract's nft balance.
  const accountBalanceRaw = decodeBalance(
    // @ts-expect-error
    useContractCall(
      account && claimContract?.address && nftPolygonContract?.address
        ? {
            abi: NFTPolygonABI,
            address: nftPolygonContract.address,
            args: [account],
            method: "balanceOfAll",
          }
        : undefined
    ) || []
  );

  const epochDetails = decodeEpochData(
    (useContractCall(
      claimContract?.address
        ? {
            abi: ClaimABI,
            address: claimContract.address,
            args: [],
            method: "getEpochs",
          }
        : undefined
    ) || [])[0],
    accountBalanceRaw
  );

  return {
    loading: accountBalanceRaw === undefined,
    account,
    loggedIn: !!account,
    epochDetails,
  };
}

export const UseContractState = createContainer(useContractStateContainer);
