import React from 'react'
import { useEthers } from '@usedapp/core'
import { Wallet, wallets } from '../hooks/config'
import { Modal } from '../modals/Modal'
import { isMobile } from 'react-device-detect'
import styled from 'styled-components'

const WalletButtonStyle = styled.button`
  border: 1px solid ${(props) => props.theme.colors.highlight};
  padding: 10px;
  width: 100%;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;

  & + button {
    margin-top: 10px;
  }

  img,
  svg {
    max-width: 20px;
    max-height: 20px;
  }
`

const WalletButton: React.FC<{ wallet: Wallet; hideModal: () => void }> = ({
  wallet: { name, icon, href, connector },
  hideModal,
}) => {
  const { activateBrowserWallet, activate } = useEthers()

  const onClick = () => {
    hideModal()
    if (connector) {
      activate(connector, undefined, true).catch(console.error)
    } else {
      activateBrowserWallet()
    }
  }

  return href ? (
    <a
      style={{ display: 'flex', border: '1px solid black', padding: '5px' }}
      className="hoverUp"
      key={name}
      href={href}
    >
      <img
        style={{
          width: 20,
          height: 20,
          marginRight: 20,
        }}
        src={icon}
      />
      {name}
    </a>
  ) : (
    <WalletButtonStyle
      style={{ display: 'flex' }}
      className="hoverUp"
      key={name}
      onClick={onClick}
    >
      <img
        style={{
          marginRight: 20,
        }}
        src={icon}
      />
      {name}
    </WalletButtonStyle>
  )
}

export const Web3Modal = ({ hideModal }: { hideModal: () => void }) => {
  let walletsToShow = wallets
  if (isMobile) {
    // On mobile, only show wallets that aren't desktop-only.
    walletsToShow = walletsToShow.filter((wallet) => !wallet.desktopOnly)
  } else {
    // On desktop, only show wallets that aren't mobile-only.
    walletsToShow = walletsToShow.filter((wallet) => !wallet.mobileOnly)
  }

  const isMetaMask =
    (window as any).ethereum && (window as any).ethereum.isMetaMask

  if (!(window as any).ethereum || isMetaMask) {
    // Filter wallets that shouldn't be shown if there's no web3 connected.
    walletsToShow = walletsToShow.filter((wallet) => !wallet.web3Only)
  }

  if ((window as any).ethereum && !isMetaMask) {
    // On web3 browsers that aren't metamask, don't show metamask.
    walletsToShow = walletsToShow.filter((wallet) => wallet.name !== 'MetaMask')
  }

  return (
    <Modal hideModal={hideModal} className="grid grid-cols-1 gap-4">
      {walletsToShow.map((wallet) => (
        <WalletButton wallet={wallet} hideModal={hideModal} />
      ))}
    </Modal>
  )
}
